require('./datetime-modal.css');

controller.$inject = ['$scope'];
function controller($scope){
  var $ctrl = this;
  $ctrl.ok = ok;
  $ctrl.cancel = cancel;

  var config = $ctrl.resolve.config;
  var date = config.date || new Date();
  var minDate = config.minDate || null;
  var maxDate = config.maxDate || null;
  var minDay = getDay(minDate);
  var maxDay = getDay(maxDate);
  console.log(config);
  // datePicker设置
  $ctrl.options = {
    showWeeks: false,
    minDate: minDate || new Date(2016,10,3,0,0,0),
    maxDate: maxDate || new Date(2016,11,20,5,5,5)
  };
  // timePicker选择框设置
  $ctrl.minTime = null;
  $ctrl.maxTime = null;

  $scope.$watch('$ctrl.date', function(newVal){
    var day = getDay($ctrl.date);
    if(minDate && minDate.getTime() > $ctrl.date.getTime()) {
      $ctrl.date.setTime(minDate.getTime());
    }
    if(maxDate && maxDate.getTime() < $ctrl.date.getTime()) {
      $ctrl.date.setTime(maxDate.getTime());
    }
    if(minDay && minDay === day) {
      $ctrl.minTime = minDate;
    } else {
      $ctrl.maxTime = null;
    }
    if(maxDay && maxDay === day) {
      $ctrl.maxTime = maxDate;
    } else {
      $ctrl.maxTime = null;
    }
    console.log(newVal);
    console.log('change!');
  });

  function getDay(date){
    if(date) {
      var d = new Date($ctrl.date.getTime());
      d.setHours(0, 0, 0);
      return d.getTime();
    }
  }

  function setDate(){
    $ctrl.date.setHours(
      $ctrl.hour, $ctrl.minute, $ctrl.second);
  }
	
	function ok(){
		$ctrl.close();
	}

	function cancel(){
		$ctrl.dismiss();
	}
}

module.exports = {
  template: require('./datetime-modal.component.html'),
  controller: controller,
  bindings: {
  	resolve: '<',
    close: '&',
    dismiss: '&'
  }
};



